<!-- 信息总览页面 -->
<template>
	<view class="content">
		<!-- 自定义状态栏 -->
		<u-navbar title="信息总览" bgColor="rgba(0,0,0,0)" :autoBack="true" leftIconColor="#000"
			:titleStyle="{color: '#000', fontWeight: 600, fontSize: '34rpx'}">
			<view class="u-nav-slot" slot="left">
				<u-icon name="arrow-left" size="19" color="#000" bold></u-icon>
			</view>
		</u-navbar>
		<view class="top-bg"></view>
		<!-- 地图 -->
		<map v-if="!loading" id="myMap" :longitude="lng" :latitude="lat" :scale="scale"
			style="width: 100%; height: 50vh;" :markers="markers" :polygons="polygons" enable-3D>
		</map>
		<view style="max-height: 50vh;overflow-y: auto;">
			<view class="tips" style="margin-top: 24rpx;">
				在库保质期临期预警
			</view>
			<view class="time-warning-box">
				<view>
					<view class="warning">
						{{'<4个月'}}
					</view>
					<view class="warning">
						{{ lateWarningData.yellowWeight || '--' }}吨
					</view>
				</view>
				<view>
					<view class="danger">
						{{'<3个月'}}
					</view>
					<view class="danger">
						{{ lateWarningData.orangeWeight || '--' }}吨
					</view>
				</view>
				<view>
					<view class="error">
						{{'<2个月'}}
					</view>
					<view class="error">
						{{ lateWarningData.redWeight || '--' }}吨
					</view>
				</view>
			</view>
			<view class="tips">
				在库率预警
			</view>
			<view v-for="item in inventoryRateWarningData" :key="item.id">
				<view class="label">
					{{ item.projectName }}
				</view>
				<view :class="['item-box', getBg(item.warningState)]">
					<view>
						任务量：{{ item.taskTotal }}吨
					</view>
					<view>
						在库量：{{ item.weightTotal }}吨
					</view>
					<view>
						在库率：{{ item.inventoryRate }}
					</view>
					<view>
						最低补货量：{{ item.replenishWeight }}吨
					</view>
				</view>
			</view>
		</view>

	</view>
</template>

<script>
	// 地图数据json
	let mapJson = null

	// 打点label样式
	const commonLabel = {
		color: '#fff',
		fontSize: 12,
		textAlign: 'center',
		anchorY: -52,
		bgColor: '#326ab3',
		padding: 2,
		borderRadius: 4,
		borderColor: '#565656'
	}

	export default {
		data() {
			return {
				// 地图是否加载完成
				mapJsonLoaded: false,
				// 地图数据
				chartData: null,
				// 临期预警
				lateWarningData: {},
				// 在库率预警
				inventoryRateWarningData: [],

				// 初始化经度
				lng: 104.672097,
				// 初始化纬度
				lat: 31.621140,
				// 放大倍数
				scale: 6.7,
				// 打点数据
				markers: [],
				// 多边形数据
				polygons: []
			}
		},
		onLoad() {
			// 初始化地图数据
			this.getMapJson()
			// 获取打点数据
			this.getChartData()
			// 获取在库保质期临期预警数据
			this.getInventoryRateWarningData()
			// 在库率预警数据
			this.getLateWarningData()
		},
		computed: {
			// 计算背景
			getBg() {
				return (warningState) => {
					const arr = ['success-bg', 'error-bg', 'danger-bg', 'warning-bg']
					return arr[Number(warningState)]
				}
			}
		},
		methods: {
			async getChartData() {
				const {
					data
				} = await uni.$u.http.post(`/screen/selectWarehouseList`, null)
				this.chartData = data

				this.markers = data?.map(i => {
					return {
						id: i.id,
						latitude: i.latitude,
						longitude: i.longitude,
						width: 17,
						height: 32,
						label: {
							content: `${i.name}(${i.currentWeight}吨)`,
							color: '#3cbf00',
							...commonLabel
						}
					}
				})
			},
			async getInventoryRateWarningData() {
				this.inventoryRateWarningData = await uni.$u.http.post(`/screen/getInventoryRateWarningData`, null, {
					params: {}
				})
			},
			async getLateWarningData() {
				this.lateWarningData = await uni.$u.http.post(`/screen/getLateWarningData`, null, {
					params: {}
				})
			},
			async getMapJson() {
				await uni.request({
					url: 'http://1.95.14.79:8080/static/510000.json',
					method: 'GET',
					success: (res) => {
						this.mapJsonLoaded = true
						this.polygons = res.data.features.map(feature => {
							const coordinates = feature.geometry.coordinates;
							const points = coordinates[0][0].map(i => {
								return {
									latitude: i[1],
									longitude: i[0],
								}
							})
							return {
								points,
								strokeWidth: 0,
								strokeColor: '#377caa',
								fillColor: `${this.getRandomColor()}26`
							}
						})




						// this.polyline = [
						// 	{
						// 		points: [{}, {}],
						// 		width: 8,
						// 		arrowLine: true,
						// 		colorList: ['#66b8b8', '#28a2a2', '#64a0c8', '#4397cf', '#377caa']
						// 	}
						// ]
						console.log('this.markers', this.polyline);
					}
				})
			},
			// 获取随机背景颜色
			getRandomColor() {
				const colors = ['#ff0004', '#ff00d5', '#5d00ff', '#0099ff', '#00f3ff', '#00ff88', '#f3ff00', '#ff9d00',
					'#003bff', '#550000'
				]
				return colors[this.getRandomInt(0, 9)]
			},
			getRandomInt(min, max) {
				return Math.floor(Math.random() * (max - min + 1) + min);
			}
		}
	}
</script>

<style lang="scss">
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		overflow: hidden;
	}

	.top-bg {
		height: 45vh;
		width: 100vw;
		background-image: linear-gradient(rgb(78, 130, 255), rgba(78, 130, 255, 0));
		z-index: -1;
		position: absolute;
		top: 0rpx;
	}

	.map-box {
		width: 100vw;
		height: 520rpx;
		margin-top: 200rpx;
	}

	.tips {
		font-family: 045, 045;
		font-weight: normal;
		font-size: 42rpx;
		color: #3487FC;
		line-height: 42rpx;
		text-align: left;
		font-style: italic;
		font-weight: 800;
		letter-spacing: 4rpx;
		text-align: center;
	}

	.time-warning-box {
		width: 688rpx;
		height: 131rpx;
		background: rgba(0, 67, 225, 0.9);
		border-radius: 15rpx;
		margin-top: 16rpx;
		margin-bottom: 30rpx;
		display: flex;
		justify-content: space-around;
		align-items: center;
		font-weight: 500;
		font-size: 27rpx;
		box-sizing: border-box;

		&>view {
			background-image: url('http://1.95.14.79:8080/static/time_limit_bg.png');
			background-size: 92%;
			flex: 1;
			height: 110%;
			background-repeat: no-repeat;
			text-align: center;
			display: flex;
			flex-direction: column;
			justify-content: center;
			background-position: 0px -83%;
		}
	}

	.label {
		font-weight: 500;
		font-size: 31rpx;
		color: #3487FC;
		text-align: left;
		width: 688rpx;
		margin-top: 12rpx;
	}

	.item-box {
		width: 688rpx;
		height: 131rpx;
		display: grid;
		grid-template-columns: 1fr 1fr;
		justify-items: start;
		align-items: center;
		padding: 19rpx;
		font-weight: 500;
		font-size: 27rpx;
		box-sizing: border-box;
		margin-top: 8rpx;
		margin-bottom: 16rpx;
		border-radius: 15rpx;
		color: #1A1A1A;
	}

	.u-nav-slot {
		background: rgba(255, 255, 255, 0.9);
		border-radius: 50%;
		padding: 10rpx;
		box-sizing: border-box;
		font-weight: 800;
		border: 1px solid #ccc;
	}
</style>